export default {
	props: {},
	data() {
		return {
			x: 0,
			move: 0,
			open: false
		}
	},
	watch: {},
	mounted() {
		let { windowWidth } = uni.getSystemInfoSync()
		this.deleteBtnWidth = ((windowWidth || 375) / 375) * 160
	},
	methods: {
		/**
		 * 删除事件
		 */
		remove() {
			this.move = 0
			this.open = false
			
			this.$emit('remove')
		},
		/**
		 * movable-view 滚动监听
		 */
		change(e) {
			this.x = e.detail.x
		},
		/**
		 * movable-view 触摸结束事件
		 */
		touchend() {
			let diff = this.x

			if (!this.open) {
				if (diff < -20) {
					this.move = -this.deleteBtnWidth
					this.open = true
				} else {
					this.move = 0
					this.open = false
				}
			} else {
				if (diff > -this.deleteBtnWidth + 20) {
					this.move = 0
					this.open = false
				} else {
					this.move = -this.deleteBtnWidth
					this.open = true
				}
			}
		}
	}
}
